<template>
  <div id="Videos">
    <h2 class="componentheader">Videos</h2>
    <p>Check out the latest videos.</p>
    <div class="video container-xl"
      v-for="(video, index) in videos"
      :key="video.id"
    >
      <div class="row">
        <div class="col-lg-3">
          <h3 class="videoTitle" @click="goToLink(index)">{{ video.title }}</h3>
          <div class="videoDesc">{{ video.description }}</div>
        </div>
        <div class="youtubeEmbed col-lg-9">
          <iframe width="560" height="315" :src="video.url" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          <!-- <img class="arrow-middle" src="images/arrow_from_top.png" alt="Arrow to Video"> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Videos',
  data () {
    return {
      selectedVideo: '',
      selectedUrl: '',
      videos: [
        {
          id: 1,
          title: 'Jinx The Crazy Eyes',
          description: 'Jinx is honestly a nutjob... no jokes',
          url: 'https://www.youtube.com/embed/tddeMtpdpL8',
          urlDirect: 'https://youtu.be/tddeMtpdpL8'
        },
        {
          id: 2,
          title: 'Draven PENTAKILL & TRIPLE KILL',
          description: 'Draven can be so deadly yet fun to play.',
          url: 'https://www.youtube.com/embed/3sht_JzUn2I',
          urlDirect: 'https://youtu.be/3sht_JzUn2I'
        },
        {
          id: 3,
          title: "It's Hammer Time with Mordekaiser",
          description: 'First time trying out a new champion is definitely daunting',
          url: 'https://www.youtube.com/embed/-3EpyQb9R6w',
          urlDirect: 'https://youtu.be/-3EpyQb9R6w'
        }
      ]
    }
  },
  methods: {
    goToLink: function (index) {
      this.selectedVideo = index
      this.selectedUrl = this.videos[this.selectedVideo].urlDirect
      location.href = this.selectedUrl
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.videoTitle:hover, .videoTitle:focus {
    transform: scale(1.1);
    font-weight: bold;
    cursor: pointer;
}

.videoDesc {
    margin-bottom: .5em;
}

.videoTitle {
  padding-bottom: .3em;
}

</style>



