<template>
    <div class="blog">
        <h2 class="componentheader">Blog</h2>
        <p>Check out the latest blog posts.</p>
        <div class="postBody"
          v-for="blogPost in blogPosts"
          :key="blogPost.id"
          >
          <h2 class="blogTitle">{{ blogPost.title }}</h2>
          <div class="blogDate">{{ blogPost.date }}</div>
          <div class="blogBody" v-html="blogPost.blogBody">
              <!-- {{ blogPost.blogBody }} -->
          </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Blog',
  data () {
    return {
      blogPosts: [
        {
          id: 1,
          date: 'June 14, 2020',
          title: 'Documenting My S2020 Ranked Journey In S2020 (League of Legends)',
          summary: 'Have you ever wanted to bash your keyboard into piece while playing in ranked games? I know I have. This is my first season playing ranked in League of Legends. Before I delve into my S2020 ranked journey, it might be interest to know how I first started playing League of Legends. ',
          blogBody: `
            <p style="font-weight:bold;font-style:italic;">Have you ever wanted to bash your keyboard into piece while playing in ranked games? I know I have.</p>
            <p>This is my first season playing ranked in League of Legends. Before I delve into my S2020 ranked journey, it might be interest to know how I first started playing League of Legends. </p>
            <p>Before my foray into League of Legends, I played a similar MMOBA (mass multi-player online battle arena) game called Heroes of the Storm, which is created by Blizzard. I really liked the game and when I talked to my friends about it, they were saying: "Why don't you play League of Legends? It is kind of the OG of MMOBA." I was hesitant at first because I already had a group of friends who play Heroes of the Storm (also known as HOTS). </p>
            <p>After a while though, I decided to just try out League of Legends. I downloaded the game probably around summer of 2019 and have been hooked ever since. I started off playing Miss Fortune in Normals because she was one of the introductory characters and I didn't care enough to check out other characters. After playing a while though, I realized that playing ADC is not exactly my strong suit. Why? Multi-tasking is not my strong suit. If I am focusing on CS-ing, then I find it challenging to also poke the enemy. The times I do play well as an ADC are largely thanks to a strong support character in the bottom lane who plays aggressively and focuses on poking the enemy champions. After this realization, I decided to switch to play support.</p>
            <p>I already mentioned one of my reasons for playing support in Normals. However, there is also another reason which might even overshadows my inept multi-tasking abilities. I don't like to be alone in a lane. If I were to play top, I think I'd quit because I would be so lonely. Yes, there is the rift herald in the beginning but besides that, I feel like there is not as much activity up in top lane. The dragon always seems to be quite contested in the beginning of the game.</p>
            <p>I do want to note that I am by no means amazing at playing any computer games, but I do enjoy playing them and I do want to get better at playing them.</p>
            <p>After almost half a year of playing ranked, I have definitely had my up's and down's with this mode. Playing ranked is the most frustrating, especially if you are playing solo. I'm currently stuck at Bronze and have jockeyed across the different tiers. When talking to other players, it definitely seems to be an anomaly to be fluctuating that much. I know you can do a duo mode but most of my friends are silver or gold after at least a couple of years of playing so it is sometimes not even possible to play together.</p>
            <p>I thought it might be cathartic to share my experience so far via this blog, and relate to some of you who have shared similar frustrations, or even entertaining to some of you by means of Schadenfreude.</p>
            <p>This is my first post. I will definitely keep you posted on my experience</p>
          `
        }
      ]
    }
  }
}
</script>



