<template>
  <div id="Art" class="container-xl">
    <h2 class="componentheader">Art</h2>
    <p>My doodling of different characters from games. Most of them are inspired by my gameplay or experience while playing the game.</p>
    <div class="container-xl">
      <div class="row">
        <div class="thumbnails col-md-6 col-lg-4"
          v-for="(thumbnail, index) in thumbnails"
          :key="thumbnail.thumbnailId"
          @click="showModal(index)"
        >
        <img v-lazy="thumbnail.url" :alt="thumbnail.title"/>
        </div>
      </div>
    </div>
    <lgImageModal v-model="modalShow" :selectedUrl="selectedUrl"></lgImageModal>
  </div>
</template>

<script>
import lgImageModal from '@/components/lgImageModal.vue'

export default {
  name: 'Art',
  components: {
    lgImageModal
  },
  data () {
    return {
      modalShow: false,
      selectedImg: 0,
      selectedUrl: '',
      thumbnails: [
        {
          thumbnailId: 12,
          title: 'Blitzcrank vs Jhin',
          url: '/static/images/Blitzcrank_Thumbnail.png'
        },
        {
          thumbnailId: 11,
          title: 'Cassiopeia Quadra Kill 1',
          url: '/static/images/Cassiopeia_Thumbnail.png'
        },
        {
          thumbnailId: 10,
          title: 'Cassiopeia Quadra Kill 2',
          url: '/static/images/Cassiopeia_Thumbnail2.png'
        },
        {
          thumbnailId: 9,
          title: 'Draven Pentakill',
          url: '/static/images/DravenPentaKill2.png'
        },
        {
          thumbnailId: 8,
          title: 'Evelynn & Ziggs',
          url: '/static/images/Evelynn_Ziggs.jpg'
        },
        {
          thumbnailId: 7,
          title: 'Heimerdinger AFK',
          url: '/static/images/Heimerdinger_AFK.png'
        },
        {
          thumbnailId: 6,
          title: 'Janna Vs. Warwick',
          url: '/static/images/JannaVsWarwick.png'
        },
        {
          thumbnailId: 5,
          title: 'Mordekaiser First Time',
          url: '/static/images/Mordekaiser_Thumbnail.png'
        },
        {
          thumbnailId: 4,
          title: 'Swain Winterfell',
          url: '/static/images/Swain_Winterfell.jpg'
        },
        {
          thumbnailId: 3,
          title: 'Vlad The Rookie',
          url: '/static/images/Vlad_TheRookie.png'
        },
        {
          thumbnailId: 2,
          title: 'Zyra Poison Ivy',
          url: '/static/images/Zyra_Thumbnail.png'
        },
        {
          thumbnailId: 1,
          title: 'Rakan The Social Distancing Violator',
          url: '/static/images/Rakan_Thumbnail.png'
        }
      ]
    }
  },
  methods: {
    showModal: function (index) {
      this.selectedImg = index
      this.selectedUrl = this.thumbnails[this.selectedImg].url
      this.modalShow = !this.modalShow
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.thumbnails img {
  width: 100%;
  margin-bottom: 1.5em;
  border-radius: 7%;
  box-shadow: 5px 5px rgba(0,0,0,.2);
}

.thumbnails img:hover, .thumbnails img:focus {
  transform: scale(1.1);
  transition-duration: .5s;
  cursor: pointer;
  box-shadow: 10px 10px rgba(193,241,0,1);
}

</style>



