<template>
  <div id="app" class="container-fluid">
    <nav class="navbar navbar-expand-lg navbar-light justify-content-end custom_nav">
      <router-link to="/" class="navbar-brand">Keep LoLing S2</router-link>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse justify-content-center" id="navbarNavDropdown">
        <ul class="navbar-nav">
          <li class="nav-item">
            <router-link to="/" class="nav-link">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/About" class="nav-link">About</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/Videos" class="nav-link">Videos</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/Art" class="nav-link">Art</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/Blog" class="nav-link">Blog</router-link>
          </li>
        </ul>
      </div>
    </nav>
    <router-view/>
    <keep-alive><Updates v-show="showHome"/></keep-alive>
    <router-link v-show="showHome" to="/Blog" class="btn readMore">Read More</router-link>
    <SocialMedia/>
  </div>
</template>

<script>
import SocialMedia from '@/components/socialmedia'
import Updates from '@/components/Updates'

export default {
  name: 'App',
  components: {
    Updates,
    SocialMedia
  },
  data () {
    return {
      component: Updates
    }
  },
  computed: {
    showHome () {
      if (this.$route.path === '/') {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



